<template>


  <div class="main" v-loading="loading">

    <H1></H1>

    <div class="input_main">
      <el-input v-model="url" placeholder=""/>
      <el-input v-model="text" placeholder="" class="d_text"/>
    </div>
    <el-button @click="save_pic" style=" margin-top: 1rem"></el-button>

    <div class="input_main">
      <input type="file" ref="file">
      <el-input v-model="text1" placeholder="" class="d_text"/>
    </div>

    <el-progress :text-inside="true" :stroke-width="18" v-bind:percentage="local_progress" color="rgba(00, 00, 00, 0.7)"
                 style="width: 30%"/>

    <el-button @click="saveLocalFile2QiNiu" style=" margin-top: 1rem"></el-button>


    <el-button v-if="pic_datas.length>0" round style="margin-left: 70%" type="success" @click="doSendServer">
    </el-button>

    <div v-for="(val ,key,index ) in pic_datas" class="list_main" v-viewer>
      <PicData :obj="val" @click_base="pic_update"/>
    </div>
  </div>


</template>

<script>
  import PicData from "./PicData";

  var qiniu = require('qiniu')
  var qiniujs = require('qiniu-js')


  export default {
    name: "ManagerAdd",
    components: {PicData},
    data() {
      return {
        pic_datas: [],
        url: 'http://hdpublic.getlove.cn/5c8fce90641ec410596f3988.',
        text: '',
        text1: '',
        local_progress: 0,
        loading: false
      }
    },
    mounted() {
      //
      let current = Bmob.User.current()
      console.log(current)
      if (!current) {
        this.$message('');
        this.$router.push('login')
        return
      }
    },
    methods: {
      save_pic() {
        if (this.url.trim().length == 0 || this.text.trim().length == 0) {
          this.$message('');
          return
        }
        console.log(' save_pic ' + this.url + this.text)
        this.pic_datas.push({'url': this.url, text: this.text})
      },
      pic_update(obj) {
        console.log(obj)
        this.pic_datas.remove(obj)
      },

      saveLocalFile2QiNiu() {

        let localFile = this.$refs.file.files[0];
        if (!localFile) {
          this.$message('file is error')
          return
        }
        if (this.text1.trim().length == 0) {
          this.$message('file description is error')
          return
        }
        this.local_progress = 0;
        var accessKey = 'd2ZWIj-W2b1NfddBDQtNRT_IsLFlIgbt1SxekFZ2';
        var secretKey = '8xv2Nb81ogMG5YCbF40NyBp2lNQ60g5MGIRROHH3';
        var mac = new qiniu.auth.digest.Mac(accessKey, secretKey);
        console.log(localFile)


        var options = {
          scope: 'vuedemo',
        };
        var putPolicy = new qiniu.rs.PutPolicy(options);
        var uploadToken = putPolicy.uploadToken(mac);

        console.log(uploadToken)

        var config = new qiniu.conf.Config();
        config.zone = qiniu.zone.Zone_z0;
        config.useCdnDomain = true;

        var putExtra = {
          fname: localFile.name,
          params: {},
          mimeType: [] || null
        };


        var observable = qiniujs.upload(localFile, localFile.name, uploadToken, putExtra, config)

        observable.subscribe(res => {
          console.log(res.total.loaded + '  ' + res.total.size + ' ' + res.total.percent)
          this.local_progress = res.total.percent;
        }, error => {
          this.$message(error);
        }, res => {
          let qn_url = 'http://po8c2o94f.bkt.clouddn.com/' + res.key;
          console.log(qn_url)
          this.local_progress = 100;
          this.$message('');

          this.pic_datas.push({'url': qn_url, text: this.text1})

        })
      },
      doSendServer() {

        this.loading = true

        const queryArray = new Array();
        // 50
        for (var i = 0; i < this.pic_datas.length; i++) {
          var queryObj = Bmob.Query('Pic_Data');
          queryObj.set('url', this.pic_datas[i].url);
          queryObj.set('description', this.pic_datas[i].text);
          queryArray.push(queryObj);
        }

        // 
        Bmob.Query('Pic_Data').saveAll(queryArray).then(result => {
          console.log(result);
          this.loading = false
          this.$message('');
          this.pic_datas.length = 0
        }).catch(err => {

          this.$message(err);
          this.loading = false
        });

      }
    }
  }
</script>

<style lang="stylus" scoped>

  .main
    display flex;
    height 100%
    flex-direction column
    background-color: #E9EEF3;
    align-items center

    .input_main
      display flex;
      width 80%
      padding 1rem

      align-items center

      .d_text
        margin-left: 1rem;

    .list_main
      width 80%
      margin-top 1rem


</style>



