<template>
  <el-container style="height: 100%">


    <el-header>
      <div class="head_main">
        <el-tabs v-model="activeName" @tab-click="handleClick" class="tabs">
          <el-tab-pane label="News" name="first"/>
          <el-tab-pane label="" name="second"/>
          <el-tab-pane label="" name="third"/>

        </el-tabs>


        <div class="top_center">
          <img src="http://po8c2o94f.bkt.clouddn.com/%E5%BE%AE%E4%BF%A1%E5%9B%BE%E7%89%87_20190304163943.png"
               width="50px" height="50px" v-on:click="go2Home"/>


          <el-dropdown placement="bottom" style="margin-left: 1rem"><span class="el-dropdown-link"><i></i></span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item></el-dropdown-item>
              <el-dropdown-item></el-dropdown-item>
              <el-dropdown-item></el-dropdown-item>
              <el-dropdown-item disabled></el-dropdown-item>
              <el-dropdown-item divided></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>

        <div class="top_right_main">
          <div>
            <el-input v-model="input" placeholder="" style="width: 200px"/>
            <i class="el-icon-search" @click="action_search"></i>
          </div>

          <el-tabs v-model="activeName" @tab-click="handleClick" class="tabs" style="margin-left: 10px">
            <el-tab-pane label="" name="five"/>

            <el-tab-pane label="" name="six"/>


          </el-tabs>

          <router-link :to="{ name: 'manager'}" target="_blank"></router-link>

        </div>
      </div>


    </el-header>
    <el-main>


      <keep-alive>
        <router-view></router-view>
      </keep-alive>


    </el-main>
  </el-container>

</template>


<script>

  import Tab1 from "./Tab1";
  import Tab2 from "./Tab2";
  import Tab3 from "./Tab3";


  export default {
    name: "Home",
    components: {Tab3, Tab2, Tab1},
    data() {
      return {
        activeName: 'first',
        input: ''
      };
    },
    methods: {
      handleClick(tab, event) {
        console.log(tab.paneName);
        this.activeName = tab.name;
        this.$router.push(this.activeName)
      },
      go2Home() {
        console.log("----gohome")
        this.activeName = 'first';
        this.$router.push(this.activeName)
      },
      action_search() {
        console.log(this.input)
      }
    },
    comments: {
      Tab1
    },

    mounted() {


      // if(this.$route.params.userId){
      //   this.activeName='seven'
      // }

      this.$router.push(this.activeName)


    }

  }

</script>

<style lang="stylus" scoped>

  .head_main
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;

    .tabs
      display: flex;
      width: 40%;

      padding-left: 24px;
      padding-right: 24px;
      justify-content: center;

    .top_center

      display flex
      flex 1
      justify-content center

    .top_right_main
      display: flex;
      width: 40%;
      align-items: center;
      justify-content: center





  .el-header {
    line-height: 60px;

    display: flex;

  }

  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
  }

  .el-dropdown-link {
    cursor: pointer;
    color: #409EFF;

  }

</style>



